class TimbreDB {
    constructor(pca, center) {
        this.mean = pca.mean;
        this.cps = pca.cps;
        this.centerEmb = center;
        this.data = [];
        this.embeddings = {"data" : [], "tag" : []};
        this.centerData = {"data" : [], "tag" : []};
        this.initialize();

    }

    initialize() {
        let centerData = this.pcaTransform(this.centerEmb);
        let tags = []
        for(let i = 0; i < centerData.length; ++i)
            tags.push((i + 1) + "");
        let centerTags = tags;
        this.centerData = {"data" : centerData, "tag" : centerTags};
    }

    tag(){
        for (let i = 0; i < this.centerData.data.length; ++ i){
            let ind = -1, minVal = 233333;
            for(let j = 0; j < this.embeddings.data.length; ++j){
                let val = 0;
                for(let k = 0; k < this.embeddings.data[j].length; ++k) {
                    val += (this.embeddings.data[j][k] - this.centerData.data[i][k])**2;
                }
                if(val < minVal){
                    minVal = val;
                    ind = j;
                }
            }
            this.centerData.tag[i] = this.embeddings.tag[ind];
        }
    }
    load_db(data){
        this.data = data;
        let tags = [];
        let embeddings = [];
        for(let i = 0; i < data.length;++ i){
            tags.push(data[i].tag);
            embeddings.push(data[i].embedding);
        }
        embeddings = this.pcaTransform(embeddings);
        this.embeddings = {"data" : embeddings, "tag" : tags};
    }

    pcaTransform(x){
        let y = [];
        for(let i = 0; i < x.length ; ++ i){
            let res = []
            for(let j = 0; j < this.cps[0].length; ++ j){
                let val = 0.;
                for(let k = 0; k < this.cps.length; ++ k){
                    val += (x[i][k] - this.mean[k]) * this.cps[k][j];
                }
                res.push(val);
            }
            y.push(res);
        }
        return y;
    }

    getCenters(){
        return this.centerData;
    }

    getEmbeddings(){
        return this.embeddings;
    }
}

function createTimbreDB(pca, center){
    let timbreDB = new TimbreDB(pca, center);
    return timbreDB;
}

export {createTimbreDB}