<template>
<div class="timbrePanelCss">
    <div class="narrowBandCss"><span>Timbre database: </span></div>
    <div class="hintCss">
      <div><b>Load</b> timbre embeddings from a database</div>
      <div><b>Tag</b> sources using timbre embeddings in the database</div>
      <div><b>Select</b> timbre embeddings to re-separate</div>
      <div><b>Save</b> current timbre embeddings</div>
    </div>
    <div class="largeBandCss">
      <el-button type="primary" ref="loadBt" size="small" :disabled="!analyzed"  @click="onClickLoad">Load</el-button>
      <input type="file" hidden ref="uploadDB" @change="onClickLoadDB">
      <el-button type="warning" ref="hideBt" plain size="small" :disabled="!analyzed" @click="onClickCancel" >{{hideDB}}</el-button>
      <el-button type="primary" size="small" :disabled="timbreDB.length == 0" @click="onClickTag">Tag</el-button>
      <el-button type="warning"  size="small" :disabled="!analyzed">Select</el-button>

  </div>
  <div class="largeBandCss">
    <div class="largeWCss">
      <el-input :disabled="!analyzed"
        v-model="instr"
        placeholder="Instrument name"
        class="input-with-select">
        <template #prepend>
        <el-select v-model="selectTrk" @change="onSelectChangeTrk" placeholder="Select" style="width: 110px" :disabled="!analyzed">
          <el-option  v-for="item in sources" :key="item.value" :label="item.label" :value="item.value" />
        </el-select>
      </template>
      </el-input>
    </div>
    <div class="narrowCss"><el-button type="danger" size="small" :disabled="!analyzed">Save</el-button></div>
  </div>
  <div class="largeBandCss">
    <div class="narrowWCss"><span>Pitch range: </span></div>
    <div class="midWCss">
      <el-slider v-model="pitchRange" range :max="pitchMax" :min="pitchMin" size="small" disabled
                  class="slider" @change="onChangePitchRange"/>
    </div>
  </div>

</div>
</template>

<script>

export default {
  name: "TimbrePanel",
  components: {
  },
  props :{
    sources : Object,
  },
  data(){
    return{
      analyzed : false,
      instr : "undefined",
      pitchRange : [1, 128],
      pitchMin : 1,
      pitchMax : 128,
      hideDB : "hide",
      timbreDB : [],
      selectTrk : 1,
//:disabled="!analyzed"
    }
  },
  methods:{
    callFunc(val){
      let func = val.func, arg = val.arg;
      this.$options.methods[func](this, arg);
    },
    setPitchRange(_this, val){
      _this.pitchMin = val[0];
      _this.pitchMax = val[1];
      _this.pitchRange = val;
    },
    setAnalyzed(_this, val){
      _this.analyzed = val.analyzed;
    },
    pianoPanelAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("pianoPanelAction", val);
    },
    timbreSpaceAction(func, arg=null){
      let val = {"func" : func, "arg" : arg};
      this.$emit("timbreSpaceAction", val);
    },
    onChangePitchRange(evt){
      //this.timbreSpaceAction("filter", [129 - this.pitchRange[1], 129 - this.pitchRange[0]]);
    },
    onSelectChangeTrk(evt){
      this.$emit("changeTrk", this.selectTrk);
    },
    onClickLoad(){
      this.$refs.uploadDB.click();

    },
    onClickLoadDB(evt){
      let file = evt.target.files[0];
      let reader = new FileReader();
      let _this = this;
      reader.onload = e => {
        let json = JSON.parse(e.target.result);
        _this.timbreDB = json;
        _this.timbreSpaceAction("loadTimbreDB", this.timbreDB);
      };
      reader.readAsText(file)
      console.log("here");

    },
    onClickCancel(evt){
      if(this.hideDB == "hide"){
        this.timbreSpaceAction("loadTimbreDB", []);
        this.hideDB = "show";
      }else{
        this.timbreSpaceAction("loadTimbreDB", this.timbreDB);
        this.hideDB = "hide";
      }
    },
    onClickTag(evt){
      this.timbreSpaceAction("tagTrks", this.timbreDB);
      this.$emit("updateSources", null);

    },

  }
}
</script>

<style lang="scss" scoped>

.timbrePanelCss{
  width: 100%;
  margin-left: 5px;
}
.largeBandCss{
  width: 100%;
  float: left;
  margin-top: 10px;
  margin-left: 20px;
}
.narrowBandCss{
  margin: 10px;
  width: 100%;
  float: left;
}
.hintCss{
  margin-left: 20px;
  width: 100%;
  float: left;
}

.midWCss{
  width: 50%;
  float: left;
}
.narrowWCss{
  padding-top: 6px;
  width: 25%;
  float: left;
}
.largeWCss{
  width: 60%;
  float: left;
}
.narrowCss{
  margin-top: 5px;
  margin-left: 5px;
  width: 30%;
  height: 20px;
  float: left;
}



</style>